.App {
  text-align: center;
}

.App-header {
  background-color: #be1506;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header p{
  font-size: 50px;
  color: coral;
  margin: 10px 0 10px 0;
}

.btn{
  background-color: coral;
  padding: 20px;
  border: none;
  border-radius: 20px;
  font-size: 20px;
  color: #ffffff;
  margin-bottom: 10px;
}

.mini-btn{
  background-color: cornflowerblue;
  padding: 5px;
  border: none;
  border-radius: 5px;
  color: blue;
  font-size: 20px;
}

.mini-btn:hover{
  box-shadow: 0px 0px 10px 5px #9dd1e9;
}

h1{
  margin: 0;
}